.page-wrapper {
	overflow : hidden;
	max-width: 1200px;
	margin: 0 auto;
	background-color: #c0dbd9;
	padding: 0 15px;
}


.main {
	display: grid;
	grid-template-columns: repeat(12,1fr);
	grid-gap: 15px;
	background-color: #b3f95a;
	counter-reset: Num;
	margin-bottom: 15px;
}

.main__item {
	background-color: #8ad191;
	min-height: 198px;
	counter-increment: Num;
	display: flex;
	align-items: center;
	justify-content: center;
	&:before {
		font-weight: 400;
		font-size: 53px;
		color: #fff;
	}

	&:nth-child(1) {
		grid-column: 1/5;

		&:before {
			content: counter(Num);
		}

	}

	&:nth-child(2) {
		grid-column: 5/11;
		&:before {
			content: counter(Num);
		}
	}

	&:nth-child(3) {
		grid-column: 11/13;
		&:before {
			content: counter(Num);
		}
	}

	&:nth-child(4) {
		grid-column: 1/11;
		&:before {
			content: counter(Num);
		}
	}

	&:nth-child(5) {
		grid-column: 11/13;
		&:before {
			content: counter(Num);
		}
	}
}

.blocks {
	background-color: #88b5bf;
	padding: 30px 30px;
	margin-bottom: 100px;
}

.blocks__grid {
	display: grid;
	grid-template-columns: repeat(12,1fr);
	grid-gap: 30px;
}

.blocks__item {
	flex: 0 0 25%;
	height: 0;
	padding-bottom: 80%;
	grid-column: 3 span;
	background-color: #0d89b0;
}


.modal {
	position: fixed;
	display: none;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.5);
	z-index: 1;
}

.modal:target {
	display: block;
}

.modal-content {
	background-color: #fefefe;
	margin: 15% auto;
	padding: 15px;
	width: 400px;
	height: 400px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 400;
	font-size: 53px;
}

.modal-close {
	position: absolute;
	top: 15px;
	right: 15px;
}

.btn {
	font-weight: 400;
	font-size: 20px;
	color: #fff;
	background-color:  #a5c7f9;
	border-radius: 5px;
	padding: 8px 15px;
	text-decoration: none;

	&:hover {
		color: #fff;
	}
}
