.page-wrapper {
  overflow: hidden;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #c0dbd9;
  padding: 0 15px;
}

.main {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 15px;
  background-color: #b3f95a;
  counter-reset: Num;
  margin-bottom: 15px;
}

.main__item {
  background-color: #8ad191;
  min-height: 198px;
  counter-increment: Num;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main__item:before {
  font-weight: 400;
  font-size: 53px;
  color: #fff;
}
.main__item:nth-child(1) {
  grid-column: 1/5;
}
.main__item:nth-child(1):before {
  content: counter(Num);
}
.main__item:nth-child(2) {
  grid-column: 5/11;
}
.main__item:nth-child(2):before {
  content: counter(Num);
}
.main__item:nth-child(3) {
  grid-column: 11/13;
}
.main__item:nth-child(3):before {
  content: counter(Num);
}
.main__item:nth-child(4) {
  grid-column: 1/11;
}
.main__item:nth-child(4):before {
  content: counter(Num);
}
.main__item:nth-child(5) {
  grid-column: 11/13;
}
.main__item:nth-child(5):before {
  content: counter(Num);
}

